#about {
  background-image: url('../img/icono.svg');
  background-repeat: no-repeat;
  background-size: 50%;
  background-position: center;
  background-attachment: fixed;
  #menu-header {
    .items {
      display: none; } }
  .texto-imagen {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 25px;
    margin: 100px 0;
    .left {
      width: 60%;
      margin: 0 auto;
      h2 {
        margin-bottom: 50px; }
      p {
        margin-top: 25px; } } }
  .texto {
    margin: 150px auto;
    width: 60%;
    text-align: center; }
  .imagen-imagen {
    display: grid;
    grid-gap: 25px;
    grid-template-columns: 1fr 1fr; } }
@media screen and (max-width: 750px) {
  #about {
    .texto-imagen {
      grid-template-columns: 1fr;
      .left {
        width: 100%;
        margin: 0 auto 50px; } }
    .texto {
      width: 100%;
      text-align: center; }
    .imagen-imagen {
      grid-gap: 25px;
      grid-template-columns: 1fr; } } }
